<template>
  <div id="rentPolishing">
    <div class="main" id="main">
      <van-cell-group>
        <van-field
          v-model="address"
          label=""
          type="text"
          readonly
          @click="searchSelect(1)"
          placeholder="">
        </van-field>
        <van-field
          v-model="form.contract_number"
          label=""
          type="text"
          placeholder=""
          icon="clear"
          @click-icon="form.contract_number = ''">
        </van-field>
        <van-field
          v-model="form.penalty"
          label=""
          type="text"
          placeholder=""
          icon="clear"
          @click-icon="form.penalty = ''">
        </van-field>
      </van-cell-group>

      <div class="changes" v-for="(key,index) in amountReceipt">
        <div class="paddingTitle">
          <span><span v-if="amountReceipt > 1">({{index + 1}})</span></span>
          <span class="colors" v-if="amountReceipt > 1" @click="deleteAmount(index,2)"></span>
        </div>
        <van-cell-group>
          <van-field
            v-model="form.receipt[index]"
            type="text"
            label=""
            placeholder="">
          </van-field>
        </van-cell-group>
      </div>
      <div @click="addAmount(2)" class="addInput">
        +
      </div>

      <div class="changes" v-for="(key,index) in amount">
        <div class="paddingTitle">
          <span><span v-if="amount > 1">({{index + 1}})</span></span>
          <span class="colors" v-if="amount > 1 && index !== 0" @click="deleteAmount(index,1)"></span>
        </div>
        <van-cell-group>
          <van-field
            v-model="form.customers[index].name"
            type="text"
            :disabled="index === 0"
            label=""
            placeholder="">
          </van-field>
          <van-field
            v-model="form.customers[index].phone"
            type="text"
            label=""
            :disabled="index === 0"
            placeholder="">
          </van-field>
          <div class="checks radio">
            <span class="req">*</span>
            <div class="checkTitle"></div>
            <van-radio-group v-model="sexs[index]">
              <van-radio name="1"></van-radio>
              <van-radio name="2"></van-radio>
            </van-radio-group>
          </div>
          <van-field
            v-model="cardName[index]"
            type="text"
            @click="selectShow(1, index)"
            readonly
            label=""
            placeholder="">
          </van-field>
          <van-field
            v-model="form.customers[index].idcard"
            type="text"
            label=""
            placeholder="">
          </van-field>
        </van-cell-group>
      </div>
      <div @click="addAmount(1)" class="addInput">
        +
      </div>
      <van-cell-group>
        <van-field
          v-model="form.water"
          label=""
          type="text"
          class="number"
          placeholder=""
          icon="clear"
          @click-icon="form.water = ''">
        </van-field>
        <div class="first_date">
          <van-field
            style="width: 110px;"
            label="">
          </van-field>
          <van-field
            v-model="form.electricity_peak"
            type="text"
            class="number"
            placeholder=""
            icon="clear"
            @click-icon="form.electricity_peak = ''">
          </van-field>
          <van-field
            class="twoBorder number"
            v-model="form.electricity_valley"
            type="text"
            placeholder=""
            icon="clear"
            @click-icon="form.electricity_valley = ''">
          </van-field>
        </div>
        <van-field
          v-model="form.gas"
          label=""
          type="text"
          class="number"
          placeholder=""
          icon="clear"
          @click-icon="form.gas = ''">
        </van-field>
        <van-field
          v-model="form.public_fee"
          label=""
          type="text"
          class="number"
          placeholder=""
          icon="clear"
          @click-icon="form.public_fee = ''">
        </van-field>
        <van-field
          v-model="form.manage_fee"
          label=""
          type="text"
          class="number"
          placeholder=""
          icon="clear"
          @click-icon="form.manage_fee = ''">
        </van-field>
        <van-field
          v-model="form.data_date"
          @click="timeChoose"
          label=""
          type="text"
          readonly
          placeholder=""
          icon="clear"
          @click-icon="form.data_date = ''">
        </van-field>
      </van-cell-group>

      <div class="aloneModel">
        <div class="title"></div>
        <UpLoad :ID="'photo1'" @getImg="getImgData" :isClear="isClear" :editImage="pics.identity_photo"></UpLoad>
      </div>
      <div class="aloneModel">
        <div class="title"></div>
        <UpLoad :ID="'photo2'" @getImg="getImgData" :isClear="isClear" :editImage="pics.photo"></UpLoad>
      </div>
      <div class="aloneModel">
        <div class="title"></div>
        <UpLoad :ID="'photo3'" @getImg="getImgData" :isClear="isClear" :editImage="pics.water_photo"></UpLoad>
      </div>
      <div class="aloneModel">
        <div class="title"></div>
        <UpLoad :ID="'photo4'" @getImg="getImgData" :isClear="isClear" :editImage="pics.electricity_photo"></UpLoad>
      </div>
      <div class="aloneModel">
        <div class="title"></div>
        <UpLoad :ID="'photo5'" @getImg="getImgData" :isClear="isClear" :editImage="pics.gas_photo"></UpLoad>
      </div>
      <div class="aloneModel">
        <div class="title"></div>
        <UpLoad :ID="'photo6'" @getImg="getImgData" :isClear="isClear" :editImage="pics.checkin_photo"></UpLoad>
      </div>
      <div class="aloneModel">
        <div class="title"></div>
        <UpLoad :ID="'photo7'" @getImg="getImgData" :isClear="isClear" :editImage="pics.certificate_photo"></UpLoad>
      </div>
      <div class="aloneModel">
        <div class="title"></div>
        <UpLoad :ID="'photo8'" @getImg="getImgData" :isClear="isClear" :editImage="pics.deposit_photo"></UpLoad>
      </div>
      <div class="aloneModel">
        <div class="title"></div>
        <UpLoad :ID="'photo9'" @getImg="getImgData" :isClear="isClear" :editImage="pics.other_photo"></UpLoad>
      </div>

      <van-cell-group>
        <van-field
          v-model="form.remark_terms"
          label=""
          type="textarea"
          placeholder=""
          icon="clear"
          @click-icon="form.remark_terms = ''">
        </van-field>
        <van-field
          v-model="form.remark"
          label=""
          type="textarea"
          placeholder=""
          icon="clear"
          @click-icon="form.remark = ''">
        </van-field>
        <van-field
          v-model="form.staff_name"
          disabled
          label=""
          type="text"
          placeholder="">
        </van-field>
        <van-field
          v-model="form.department_name"
          disabled
          label=""
          type="text"
          placeholder="">
        </van-field>
      </van-cell-group>
    </div>

    <div class="footer">
      <div class="" @click="close_()"></div>
      <div class="" @click="saveCollect(0)"></div>
      <div class="" @click="saveCollect(1)"></div>
    </div>

    <van-popup :overlay-style="{'background':'rgba(0,0,0,.2)'}" v-model="selectHide" position="bottom" :overlay="true">
      <van-picker
        show-toolbar
        :columns="columns"
        @cancel="onCancel"
        @confirm="onConfirm"/>
    </van-popup>

    <!---->
    <van-popup :overlay-style="{'background':'rgba(0,0,0,.2)'}" v-model="timeShow" position="bottom" :overlay="true">
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        :min-date="minDate"
        :max-date="maxDate"
        @change="monthDate"
        @cancel="onCancel"
        @confirm="onDate"/>
    </van-popup>
  </div>
</template>

<script>
  import UpLoad from '../../common/UPLOAD.vue'
  import {Toast} from 'vant';

  export default {
    name: "collect",
    components: {UpLoad, Toast},
    data() {
      return {
        haveInHand: true,
        urls: globalConfig.server,
        selectHide: false,        //select
        isClear: false,           //
        picStatus: true,

        minDate: new Date(2000, 0, 1),
        maxDate: new Date(2200, 12, 31),
        currentDate: '',
        timeShow: false,          //
        timeValue: '',            //value
        amount: 1,
        tabs: '',
        tabIndex: '',
        columns: [],

        prove_name: [],
        prove_all: [],

        cardName: [],

        amountReceipt: 1,                  //
        receiptDate: '',

        address: '',
        contract_id: '',                    //id
        sexs: [''],
        form: {
          is_submit: 1,
          penalty: '',                      //
          house_id: '',
          contract_number: '',              //
          receipt: [],                    //
          customers: [{
            id: '',                         //ID
            name: '',                       //
            phone: '',                      //
            sex: '',                        //
            idtype: '',                     //
            idcard: '',                     //
          }],
          water: '',                        //
          electricity_peak: '',             //
          electricity_valley: '',           //
          gas: '',                          //
          public_fee: '',                   //
          manage_fee: '',                   //
          data_date: '',                    //
          remark_terms: '',                 //
          remark: '',                       //
          album: {
            identity_photo: [],             //
            photo: [],                      //
            water_photo: [],                //
            electricity_photo: [],          //
            gas_photo: [],                  //
            checkin_photo: [],              //
            certificate_photo: [],          //
            deposit_photo: [],              //
            other_photo: [],                //
          },
          staff_id: '',                     //id
          department_id: '',                //id
          staff_name: '',                   //name
          department_name: '',              //name
        },
        pics: {
          identity_photo: {},               //
          photo: {},                        //
          water_photo: {},                  //
          electricity_photo: {},            //
          gas_photo: {},                    //
          checkin_photo: {},                //
          certificate_photo: {},            //
          deposit_photo: {},                //
          other_photo: {},                  //
        },
      }
    },
    mounted() {
      this.getNowFormatDate();
      this.dicts();
    },
    activated() {
      this.houseInfo();
      this.routerIndex('');
      this.ddRent('');
    },
    methods: {
      userInfo() {
        // let per = JSON.parse(sessionStorage.personal);
        // this.form.staff_id = per.id;
        // this.form.staff_name = per.name;
        // this.form.department_id = per.department_id;
        // this.form.department_name = per.department_name;
        this.form.staff_id = '';
        this.form.staff_name = '';
        this.form.department_id = '';
        this.form.department_name = '';
      },
      dicts() {
        this.receiptNum();
        // 
        this.dictionary(409, 1).then((res) => {
          this.prove_name = [];
          this.prove_all = res.data;
          for (let i = 0; i < res.data.length; i++) {
            this.prove_name.push(res.data[i].dictionary_name);
          }
        });
      },

      receiptNum() {
        // 
        this.form.receipt = [];
        this.$http.get(this.urls + 'setting/others/ip_address').then((res) => {
          if (res.data.code === '1000120') {
            // 
            this.receiptDate = res.data.data.py + res.data.data.year;
            let receipt =  res.data.data.py + res.data.data.year;
            this.form.receipt.push(receipt);
          }
        });
      },

      // 
      addAmount(val) {
        if (val === 1) {
          this.amount++;
          let data = {
            id: '',                       //ID
            name: '',                     //
            phone: '',                    //
            sex: '',                      //
            idtype: '',                   //
            idcard: '',                   //
          };
          this.sexs.push('');
          this.form.customers.push(data);
        } else {
          this.amountReceipt++;
          this.form.receipt.push(this.receiptDate);
        }
      },
      // 
      deleteAmount(index, val) {
        if (val === 1) {
          if (this.amount > 1) {
            this.amount--;
            this.form.customers.splice(index, 1);
            this.sexs.splice(index, 1);
          }
        } else {
          this.amountReceipt--;
          this.form.receipt.splice(index, 1);
        }
      },

      searchSelect(val) {
        switch (val) {
          case 1:
            this.$router.push({path: '/collectHouse', query: {type: 'renter'}});
            break;
          case 2:
            this.$router.push({path: '/organize'});
            break;
          case 4:
            this.$router.push({path: '/depart'});
            break;
        }
      },
      // 
      getNowFormatDate() {
        let date = new Date();
        let year = date.getFullYear();
        let month = date.getMonth();
        let strDate = date.getDate();
        this.currentDate = new Date(year, month, strDate);
      },
      // 
      timeChoose(val) {
        setTimeout(() => {
          this.timeShow = true;
        }, 200);
      },
      // 
      monthDate(peaker) {
        this.timeValue = peaker.getValues().join('-');
      },
      // 
      onDate() {
        this.form.data_date = this.timeValue;
        this.timeShow = false;
      },
      // 
      getImgData(val) {
        this.picStatus = !val[2];
        switch (val[0]) {
          case 'photo1'://
            this.form.album.identity_photo = val[1];
            break;
          case 'photo2'://
            this.form.album.photo = val[1];
            break;
          case 'photo3'://
            this.form.album.water_photo = val[1];
            break;
          case 'photo4'://
            this.form.album.electricity_photo = val[1];
            break;
          case 'photo5'://
            this.form.album.gas_photo = val[1];
            break;
          case 'photo6'://
            this.form.album.checkin_photo = val[1];
            break;
          case 'photo7'://
            this.form.album.certificate_photo = val[1];
            break;
          case 'photo8'://
            this.form.album.deposit_photo = val[1];
            break;
          case 'photo9'://
            this.form.album.other_photo = val[1];
            break;
        }
      },
      // select 
      selectShow(val, index) {
        this.tabs = val;
        this.tabIndex = index;
        setTimeout(() => {
          this.selectHide = true;
        }, 200);
        switch (val) {
          case 1:
            this.columns = this.prove_name;
            break;
          case 2:
            this.columns = this.cities;
            break;
        }
      },
      // select
      onConfirm(value) {
        switch (this.tabs) {
          case 1:
            this.cardName[this.tabIndex] = value;
            for (let i = 0; i < this.prove_all.length; i++) {
              if (this.prove_all[i].dictionary_name === value) {
                this.form.customers[this.tabIndex].idtype = this.prove_all[i].id;
              }
            }
            break;
          case 2:
            this.form.receipt[this.tabIndex].city = value;
            break;
        }
        this.onCancel();
      },
      // select
      onCancel() {
        this.selectHide = false;
        this.timeShow = false;
      },

      saveCollect(val) {
        if (this.contract_id !== '') {
          if (this.picStatus) {
            if (this.haveInHand) {
              this.haveInHand = false;
              let receipt = [];
              for (let i = 0; i < this.form.receipt.length; i++) {
                if (this.form.receipt[i] !== this.receiptDate) {
                  receipt.push(this.form.receipt[i]);
                }
              }
              this.amountReceipt = receipt.length === 0 ? 1 : receipt.length;
              this.form.receipt = receipt;
              for (let i = 0; i < this.sexs.length; i++) {
                this.form.customers[i].sex = this.sexs[i];
              }
              this.form.is_submit = val;
              this.$http.put(this.urls + 'bulletin/complete/rent/' + this.contract_id, this.form).then((res) => {
                this.haveInHand = true;
                if (res.data.code === '51610') {
                  if (receipt.length === 0) {
                    this.form.receipt = [];
                    this.form.receipt.push(this.receiptDate);
                  }
                  Toast.success(res.data.msg);
                } else {
                  Toast(res.data.msg);
                }
              })
            } else {
              Toast('...');
            }
          } else {
            Toast('...');
          }
        } else {
          Toast('...');
        }
      },

      houseInfo() {
        let t = this.$route.query;
        if (t.house !== undefined && t.house !== '') {
          let val = JSON.parse(t.house);
          this.contract_id = val.id;
          this.address = val.house_name;
          this.form.house_id = val.house_id;
          this.form.staff_name = val.staff_name;
          this.form.department_name = val.department_name;
          this.form.staff_id = val.staff_id;
          this.form.department_id = val.department_id;
          this.rentDetail(val.id);
        }
        // if (t.staff !== undefined && t.staff !== '') {
        //   let val = JSON.parse(t.staff);
        //   this.form.staff_id = val.staff_id;
        //   this.form.staff_name = val.staff_name;
        //   this.form.department_id = val.depart_id;
        //   this.form.department_name = val.depart_name;
        //   this.stick();
        // }
        // if (t.depart !== undefined && t.depart !== '') {
        //   let val = JSON.parse(t.depart);
        //   this.department_name = val.name;
        //   this.form.department_id = val.id;
        //   this.stick();
        // }
        // if (t.tops === '') {
        //   this.stick();
        // }
      },

      rentDetail(id) {
        this.picClose();
        this.$http.get(this.urls + 'bulletin/complete/rent/' + id).then((res) => {
          if (res.data.code === '51610') {
            let data = res.data.data;
            this.form.customers = [];
            for (let key in this.form) {
              for (let item in data) {
                if (key === item) {
                  if (item !== 'album' && item !== 'customers' && item !== 'receipt') {
                    this.form[key] = data[item] !== null ? data[item] : '';
                  }
                  if (item === 'receipt') {
                    if (typeof data[item] !== "string") {
                      if (data[item].length !== 0) {
                        this.amountReceipt = data[item].length;
                        this.form.receipt = [];
                        for (let i = 0; i < data[item].length; i++) {
                          this.form.receipt.push(data[item][i]);
                        }
                      } else {
                        this.amountReceipt = 1;
                        this.form.receipt = [];
                        this.form.receipt[0] = this.receiptDate;
                      }
                    } else {
                      this.amountReceipt = 1;
                      this.form.receipt = [];
                      this.form.receipt[0] = data[item];
                    }
                  }
                  if (item === 'customers') {
                    this.cardName = [];
                    this.sexs = [];
                    this.amount = data.customers.length;
                    for (let i = 0; i < data.customers.length; i++) {
                      let obj = {};
                      let cus = data.customers[i];
                      this.form.customers.push(obj);
                      this.sexs.push('');
                      this.form.customers[i].id = cus.id !== null ? cus.id : '';
                      this.form.customers[i].name = cus.name !== null ? cus.name : '';
                      this.form.customers[i].phone = cus.phone !== null ? cus.phone : '';
                      this.form.customers[i].sex = cus.sex !== null ? String(cus.sex) : '';
                      this.sexs[i] = cus.sex !== null ? String(cus.sex) : '';
                      this.form.customers[i].idtype = cus.idtype !== null ? cus.idtype : '';
                      this.form.customers[i].idcard = cus.idcard !== null ? cus.idcard : '';
                      for (let j = 0; j < this.prove_all.length; j++) {
                        if (this.prove_all[j].id === cus.idtype) {
                          this.cardName[i] = this.prove_all[i].dictionary_name;
                        }
                      }
                    }
                  }
                  if (item === 'album') {
                    for (let pic in data[item]) {
                      for (let pics in this.pics) {
                        if (pics === pic) {
                          this.pics[pics] = data[item][pic];
                          for (let i = 0; i < data[item][pic].length; i++) {
                            this.form[key][pics].push(data[item][pic][i].id);
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          } else {
            Toast(res.data.msg);
          }
        })
      },

      close_() {
        this.userInfo();
        this.picStatus = true;
        this.amount = 1;
        this.cardName = [];
        this.address = '';
        this.contract_id = '';
        this.sexs = [''];
        this.form.house_id = '';

        this.amountReceipt = 1;
        this.form.receipt = [];
        this.form.receipt[0] = this.receiptDate;

        this.form.contract_number = '';
        this.form.penalty = '';
        this.form.customers = [{
          id: '',                         //ID
          name: '',                       //
          phone: '',                      //
          sex: '',                        //
          idtype: '',                     //
          idcard: '',                     //
        }];
        this.form.water = '';               //
        this.form.electricity_peak = '';    //
        this.form.electricity_valley = '';  //
        this.form.gas = '';                 //
        this.form.public_fee = '';          //
        this.form.manage_fee = '';          //
        this.form.data_date = '';           //
        this.form.remark_terms = '';        //
        this.form.remark = '';              //
        this.picClose();
      },
      picClose() {
        this.isClear = true;
        setTimeout(() => {
          this.isClear = false;
        });
        $('.imgItem').remove();
        this.form.album.identity_photo = [];                //
        this.form.album.photo = [];                         //
        this.form.album.water_photo = [];                   //
        this.form.album.electricity_photo = [];             //
        this.form.album.gas_photo = [];                     //
        this.form.album.checkin_photo = [];                 //
        this.form.album.certificate_photo = [];             //
        this.form.album.deposit_photo = [];                 //
        this.form.album.other_photo = [];                   //

        this.pics.identity_photo = {};                //
        this.pics.photo = {};                         //
        this.pics.water_photo = {};                   //
        this.pics.electricity_photo = {};             //
        this.pics.gas_photo = {};                     //
        this.pics.checkin_photo = {};                 //
        this.pics.certificate_photo = {};             //
        this.pics.deposit_photo = {};                 //
        this.pics.other_photo = {};                   //
      },
    },
  }
</script>

<style lang="scss">
  #rentPolishing {
    overflow: hidden;
  }
</style>



