<template>
  <div id="quality">
    <div class="main" id="main">
      <van-cell-group>
        <div class="checks">
          <div style="min-width: 110px;margin-left: -7px"><span style="color: red;">*</span></div>
          <van-radio-group :disabled="counts === '2' || counts === '21'" v-model="form.quality_up"
                           @change="qualityChange">
            <van-radio name="0"></van-radio>
            <van-radio name="1"></van-radio>
          </van-radio-group>
        </div>
      </van-cell-group>
      <van-cell-group>
        <van-field
          v-if="followUp"
          v-model="house_name"
          label=""
          type="text"
          @click="searchSelect(3)"
          readonly
          placeholder=""
          required>
        </van-field>
        <van-field
          v-model="form.city_name"
          label=""
          @click="selectShow(2)"
          type="text"
          :disabled="followUp"
          :readonly="!followUp"
          placeholder=""
          required>
        </van-field>
        <van-field
          v-model="community_name"
          label=""
          @click="searchSelect(1)"
          type="text"
          :disabled="followUp"
          :readonly="!followUp"
          placeholder=""
          required>
        </van-field>
        <div class="fourth">
          <van-field
            label=""
            required>
          </van-field>
          <van-field
            style="width: 22%"
            v-model="form.door_address[0]"
            type="text"
            :disabled="followUp"
            placeholder="">
          </van-field>
          <span class="cut">-</span>
          <van-field
            style="width: 22%"
            v-model="form.door_address[1]"
            type="text"
            :disabled="followUp"
            placeholder="">
          </van-field>
          <span class="cut">-</span>
          <van-field
            class="twoBorder"
            v-model="form.door_address[2]"
            type="text"
            :disabled="followUp"
            placeholder="">
          </van-field>
        </div>
        <van-field
          @click="selectShow(1)"
          v-model="house_type_name"
          :readonly="!followUp"
          type="text"
          :disabled="followUp"
          label=""
          placeholder=""
          required>
        </van-field>

        <van-field
          v-model="form.area"
          label=""
          required
          type="text"
          class="number"
          :disabled="followUp"
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(14)"
          v-model="form.decorate.name"
          label=""
          :disabled="followUp"
          required
          :readonly="!followUp"
          type="text"
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(15)"
          v-model="form.property_type.name"
          label=""
          required
          :disabled="followUp"
          :readonly="!followUp"
          type="text"
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(3)"
          v-model="form.direction.name"
          label=""
          required
          :disabled="followUp"
          :readonly="!followUp"
          type="text"
          placeholder="">
        </van-field>
        <div class="first_date">
          <van-field
            style="width: 110px;"
            class="title"
            label=""
            required>
          </van-field>
          <van-field
            v-model="form.floor"
            type="text"
            class="number"
            :disabled="followUp"
            placeholder="">
          </van-field>
          <van-field
            class="twoBorder number"
            v-model="form.floors"
            type="text"
            :disabled="followUp"
            placeholder="">
          </van-field>
        </div>
        <van-field
          v-model="form.price"
          label=""
          required
          type="text"
          :disabled="followUp"
          placeholder="">
        </van-field>

        <!--<div class="titleSwitch">-->
        <!--<div class="cellGroup">-->
        <!--<span class="requiredIcon">*</span>-->
        <!--<van-switch-cell v-model="cusFrom" title=""/>-->
        <!--</div>-->
        <!--</div>-->

        <van-field
          v-model="cusFrom"
          @click="selectShow(16)"
          label=""
          type="text"
          readonly
          :disabled="followUp"
          placeholder=""
          required>
        </van-field>

        <van-field
          @click="selectShow(4)"
          v-model="air_condition_name"
          label=""
          type="text"
          readonly
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(5)"
          v-model="fridge_name"
          label=""
          type="text"
          readonly
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(6)"
          v-model="television_name"
          label=""
          type="text"
          readonly
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(7)"
          v-model="gas_stove_name"
          label=""
          type="text"
          readonly
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(8)"
          v-model="hood_name"
          label=""
          type="text"
          readonly
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(9)"
          v-model="microwave_name"
          label=""
          type="text"
          readonly
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(10)"
          v-model="wash_machine_name"
          label=""
          type="text"
          readonly
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(11)"
          v-model="water_heater_name"
          label=""
          type="text"
          readonly
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(12)"
          v-model="sofa_name"
          label=""
          type="text"
          readonly
          placeholder="">
        </van-field>
        <van-field
          @click="selectShow(13)"
          v-model="clothe_rack_name"
          label=""
          type="text"
          readonly
          placeholder="">
        </van-field>
        <div class="unit">
          <van-field
            v-model="form.dining_table"
            label=""
            type="text"
            class="number"
            placeholder="">
          </van-field>
          <b></b>
        </div>
        <div class="unit">
          <van-field
            v-model="form.chair"
            label=""
            type="text"
            class="number"
            placeholder="">
          </van-field>
          <b></b>
        </div>

        <van-switch-cell v-model="heaterOn" title=""/>
        <van-switch-cell v-model="gasOn" title=""/>
        <van-switch-cell v-model="is_cleanOn" title=""/>
        <van-switch-cell v-model="bedOn" title="+"/>

        <van-field
          v-if="!bedOn"
          v-model="form.bed_remark"
          label=""
          required
          type="textarea"
          placeholder="+">
        </van-field>

        <van-switch-cell v-model="wardrobeOn" title=""/>

        <van-field
          v-if="!wardrobeOn"
          v-model="form.wardrobe_remark"
          label=""
          required
          type="textarea"
          placeholder="">
        </van-field>

        <van-switch-cell v-model="curtainOn" title=""/>

        <van-field
          v-if="!curtainOn"
          v-model="form.curtain_remark"
          label=""
          required
          type="textarea"
          placeholder="">
        </van-field>

        <van-switch-cell v-model="is_fillOn" title=""/>

        <van-switch-cell v-if="!is_fillOn" v-model="is_lordOn" title=""/>
        <van-field
          v-if="!is_fillOn && is_lordOn"
          v-model="form.is_lord_fill_days"
          label=""
          required
          type="text"
          placeholder="">
        </van-field>

        <van-field
          v-model="form.other_furniture"
          label=""
          type="textarea"
          placeholder="">
        </van-field>
        <van-field
          v-model="form.other_remark"
          label=""
          type="textarea"
          placeholder="">
        </van-field>
      </van-cell-group>

      <div class="aloneModel required">
        <div class="title"><span>*</span></div>
        <UpLoad :ID="'headman'" @getImg="myGetImg" :isClear="isClear" :editImage="photos"></UpLoad>
      </div>

      <van-cell-group>
        <van-field
          @click="searchSelect(2)"
          v-model="form.staff_name"
          label=""
          type="text"
          placeholder=""
          required
          readonly>
        </van-field>
        <van-field
          @click="searchSelect(4)"
          v-model="form.department_name"
          label=""
          type="text"
          placeholder=""
          required
          readonly>
        </van-field>
      </van-cell-group>
    </div>

    <div class="footer" v-if="counts === '2' || counts === '21'">
      <div @click="saveCollect(0)"></div>
    </div>

    <div class="footer" v-if="counts === '1' || counts === '11'">
      <div @click="close_()"></div>
      <div @click="saveCollect(1)"></div>
      <div @click="saveCollect(0)"></div>
    </div>

    <van-popup :overlay-style="{'background':'rgba(0,0,0,.2)'}" v-model="selectHide" position="bottom" :overlay="true">
      <van-picker
        show-toolbar
        :columns="columns"
        @cancel="onCancel"
        @confirm="onConfirm"/>
    </van-popup>

  </div>
</template>

<script>
  import UpLoad from '../../common/UPLOAD.vue'
  import {Toast} from 'vant';

  export default {
    name: "index",
    components: {UpLoad, Toast},
    data() {
      return {
        haveInHand: true,
        urls: globalConfig.server,
        selectHide: false,
        columns: [],                        //select
        tabs: '',

        isClear: false,                     //
        picStatus: true,

        allCity: [],                        //
        cities: [],                         //
        beforeCity: '',                     //
        beforeCityId: '',                   //ID
        beforeProvinceId: '',               //ID
        house_type_name: '111',
        refundSta: true,

        cusFrom: '',                        //
        heaterOn: true,                     //
        gasOn: true,                        //
        is_cleanOn: true,                   //
        bedOn: true,                        //+
        wardrobeOn: true,                   //
        curtainOn: true,                    //
        is_fillOn: true,                    //
        is_lordOn: true,                    //

        air_condition_name: '1',           //
        fridge_name: '1',                  //
        television_name: '1',              //
        gas_stove_name: '1',               //
        hood_name: '1',                    //
        microwave_name: '1',               //
        wash_machine_name: '1',            //
        water_heater_name: '1',            //
        sofa_name: '1',                    //
        clothe_rack_name: '1',             //

        decorateAll: [],
        decorate_name: [],

        propertyAll: [],
        property_name: [],

        house_name: '',
        form: {
          id: '',
          processable_id: '',
          house_id: '',
          quality_up: '0',
          is_draft: 0,
          city_id: '',                  //
          city_name: '',                //
          community: {},                //id
          door_address: ['', '', ''],
          house_type: [1, 1, 1],
          area: '',                     //
          direction: {
            id: '',                     //
            name: '',
          },
          decorate: {
            id: '',                    //
            name: '',
          },
          property_type: {
            id: '',                     //
            name: '',
          },
          floor: '',                    //
          floors: '',                   //
          price: '',                    //
          is_agency: '',                 //
          air_condition: 1,             //
          fridge: 1,                    //
          television: 1,                //
          gas_stove: 1,                 //
          hood: 1,                      //
          microwave: 1,                 //
          wash_machine: 1,              //
          water_heater: 1,              //
          sofa: 1,                      //
          clothe_rack: 1,               //
          heater: 1,                    //
          gas: 1,                       //
          bed: 1,                       //
          bed_remark: '',               //
          wardrobe: 1,                  //
          wardrobe_remark: '',          //
          curtain: 1,                   //
          curtain_remark: '',           //
          is_fill: 1,                   //
          is_lord_fill: 0,              //
          is_lord_fill_days: '',        //

          dining_table: '',             //
          chair: '',                    //
          is_clean: 1,                  //
          other_remark: '',             //
          other_furniture: '',          //
          photo: [],                    //
          staff_id: '',
          department_id: '',
          staff_name: '',               //name
          department_name: '',          //name
        },
        community_name: '',
        photos: [],                     //

        isValue1: true,
        numbers: '',

        followUp: false,                    //
        counts: '',
      }
    },
    mounted() {
      let count = sessionStorage.count;
      if (count === '11') {
        this.routerIndex('');
        this.ddRent('');
        this.close_();
        this.dicts('');
      }
    },
    activated() {
      let count = sessionStorage.count;
      this.counts = count;

      if (count === '11') {
        this.routerIndex('');
        this.ddRent('');
      }
      if (count === '1') {
        this.routerIndex('');
        this.ddRent('');
        this.close_();
        this.dicts('');
        count = count + '1';
        sessionStorage.setItem('count', count);
      }
      if (count === '21') {
        let newID = JSON.parse(sessionStorage.process);
        if (newID.type === 2) {
          this.routerTo('/publishDetail', newID.ids);
        } else {
          this.counts = '1';
          this.routerIndex('');
          this.ddRent('');
        }
      }
      if (count === '2') {
        sessionStorage.setItem('process', JSON.stringify(this.$route.query));
        let newID = JSON.parse(sessionStorage.process);
        if (newID.type === 2) {
          this.close_();
          this.routerTo('/publishDetail', newID.ids);
        } else {
          this.counts = '1';
          this.close_();
          this.routerIndex('');
          this.ddRent('');
        }
        this.close_();
        this.dicts(newID);
        count = count + '1';
        sessionStorage.setItem('count', count);
      }
      this.houseInfo();
    },

    methods: {
      qualityChange(val) {
        if (this.numbers !== val) {
          this.close_();
          this.numbers = val;
          this.followUp = val === '0' ? false : true;
        }
      },
      userInfo(val1) {
        if (val1) {
          let per = JSON.parse(sessionStorage.personal);
          this.form.staff_id = per.id;
          this.form.staff_name = per.name;
          this.form.department_id = per.department_id;
          this.form.department_name = per.department_name;
        }
      },
      dicts(val) {
        // 
        this.dictionary(306, 1).then((res) => {
          this.cities = [];
          this.allCity = res.data;
          for (let i = 0; i < res.data.length; i++) {
            this.cities.push(res.data[i].dictionary_name);
          }

          this.$http.get(this.urls + 'setting/others/ip_address').then((res) => {
            if (res.data.code === '1000120') {
              this.form.province_id = '';
              this.form.city_id = '';
              let address = res.data.data.data[2] + '';
              this.form.city_name = address;
              this.beforeCity = address;
              for (let i = 0; i < this.allCity.length; i++) {
                if (this.allCity[i].dictionary_name === address) {
                  if (this.allCity[i].variable.is_province) {
                    this.form.province_id = this.allCity[i].variable.city_id;
                    this.beforeProvinceId = this.allCity[i].variable.city_id;
                  } else {
                    this.form.city_id = this.allCity[i].variable.city_id;
                    this.beforeCityId = this.allCity[i].variable.city_id;
                  }
                }
              }
            }
          });

          // 
          this.dictionary(404, 1).then((res) => {
            this.decorate_name = [];
            this.decorateAll = res.data;
            for (let i = 0; i < res.data.length; i++) {
              this.decorate_name.push(res.data[i].dictionary_name);
            }
            // 
            this.dictionary(410, 1).then((res) => {
              this.property_name = [];
              this.propertyAll = res.data;
              for (let i = 0; i < res.data.length; i++) {
                this.property_name.push(res.data[i].dictionary_name);
              }
              this.qualityDetail(val);
            });

          });

        });
      },

      // select
      onCancel() {
        this.selectHide = false;
      },
      searchSelect(val) {
        switch (val) {
          case 1:
            if (this.form.city_id !== '') {
              this.$router.push({path: '/citySearch', query: {city: this.form.city_id, type: 1}});
            } else if (this.form.province_id !== '') {
              this.$router.push({path: '/citySearch', query: {city: this.form.province_id, type: 2}});
            } else {
              Toast('');
            }
            break;
          case 2:
            this.$router.push({path: '/organize'});
            break;
          case 3:
            this.$router.push({path: '/collectHouse', query: {type: 'quality'}});
            break;
          case 4:
            this.$router.push({path: '/depart'});
            break;
        }
      },

      selectShow(val) {
        this.tabs = val;
        setTimeout(() => {
          this.selectHide = true;
        }, 200);
        switch (val) {
          case 1:
            this.columns = [
              {
                values: dicts.value1,
                className: 'column1',
                defaultIndex: 0
              },
              {
                values: dicts.value2,
                className: 'column2',
                defaultIndex: 1
              },
              {
                values: dicts.value3,
                className: 'column3',
                defaultIndex: 1
              }
            ];
            break;
          case 2:
            this.columns = this.cities;
            break;
          case 3:
            this.columns = dicts.value6;
            break;
          case 7:
            this.columns = dicts.value4;
            break;
          case 8:
            this.columns = dicts.value4;
            break;
          case 12:
            this.columns = dicts.value4;
            break;
          case 13:
            this.columns = dicts.value4;
            break;
          case 14:
            this.columns = this.decorate_name;
            break;
          case 15:
            this.columns = this.property_name;
            break;
          case 16:
            this.columns = dicts.value8;
            break;
          default:
            this.columns = dicts.value5;
        }
      },

      onConfirm(value, index) {
        switch (this.tabs) {
          case 1:
            if (value[1] === '') {
              value[1] = '0';
            }
            if (value[2] === '') {
              value[2] = '0';
            }
            this.house_type_name = value.join('');
            index[0] = index[0] + 1;
            this.form.house_type = index;
            break;
          case 2:
            this.form.city_id = '';
            this.form.province_id = '';
            for (let i = 0; i < this.allCity.length; i++) {
              if (this.allCity[i].dictionary_name === value) {
                if (this.allCity[i].variable.is_province) {
                  this.form.province_id = this.allCity[i].variable.city_id;
                } else {
                  this.form.city_id = this.allCity[i].variable.city_id;
                }
              }
            }
            this.form.city_name = value;
            this.form.community = {};
            this.community_name = '';
            break;
          case 3:
            this.form.direction.id = index + 1;
            this.form.direction.name = value;
            break;
          case 4: //
            this.air_condition_name = value;
            this.form.air_condition = index;
            break;
          case 5: //
            this.fridge_name = value;
            this.form.fridge = index;
            break;
          case 6: //
            this.television_name = value;
            this.form.television = index;
            break;
          case 7: //
            this.gas_stove_name = value;
            this.form.gas_stove = index;
            break;
          case 8: //
            this.hood_name = value;
            this.form.hood = index;
            break;
          case 9: //
            this.microwave_name = value;
            this.form.microwave = index;
            break;
          case 10: //
            this.wash_machine_name = value;
            this.form.wash_machine = index;
            break;
          case 11: //
            this.water_heater_name = value;
            this.form.water_heater = index;
            break;
          case 12: //
            this.sofa_name = value;
            this.form.sofa = index;
            break;
          case 13: //
            this.clothe_rack_name = value;
            this.form.clothe_rack = index;
            break;
          case 14: //
            this.form.decorate.name = value;
            for (let i = 0; i < this.decorateAll.length; i++) {
              if (this.decorateAll[i].dictionary_name === value) {
                this.form.decorate.id = this.decorateAll[i].id;
              }
            }
            break;
          case 15: //
            this.form.property_type.name = value;
            for (let i = 0; i < this.propertyAll.length; i++) {
              if (this.propertyAll[i].dictionary_name === value) {
                this.form.property_type.id = this.propertyAll[i].id;
              }
            }
            break;
          case 16:
            this.form.is_agency = index;
            this.cusFrom = value;
            break;
        }
        this.selectHide = false;
      },

      payWayClick(val) {
        if (val === 1) {
          this.payStatus = !this.payStatus;
          this.priceStatus = false;
        } else {
          this.priceStatus = !this.priceStatus;
          this.payStatus = false;
        }
      },

      // 
      myGetImg(val) {
        this.picStatus = !val[2];
        this.form.photo = val[1];
      },

      saveCollect(val) {
        if (this.picStatus) {
          if (this.haveInHand) {
            this.haveInHand = false;
            this.form.heater = this.heaterOn ? 1 : 0;                 //
            this.form.gas = this.gasOn ? 1 : 0;                       //
            this.form.is_clean = this.is_cleanOn ? 1 : 0;             //
            this.form.bed = this.bedOn ? 1 : 0;                       //+
            this.form.wardrobe = this.wardrobeOn ? 1 : 0;             //
            this.form.curtain = this.curtainOn ? 1 : 0;               //
            this.form.is_fill = this.is_fillOn ? 1 : 0;               //
            this.form.is_lord_fill = this.is_lordOn ? 1 : 0;          //
            this.form.is_draft = val;
            this.$http.post(this.urls + 'bulletin/quality', this.form).then((res) => {
              this.haveInHand = true;
              if (res.data.code === "51410" || res.data.code === "51430") {
                Toast.success(res.data.msg);
                this.close_();
                $('.imgItem').remove();
                this.routerDetail(res.data.data.data.id);
              } else if (res.data.code === "51420") {
                this.form.id = res.data.data.id;
                Toast.success(res.data.msg);
              } else {
                Toast(res.data.msg);
              }
            })
          } else {
            Toast('...');
          }
        } else {
          Toast('...');
        }
      },

      houseInfo() {
        let t = this.$route.query;
        if (t.house !== undefined && t.house !== '') {
          let val = JSON.parse(t.house);
          this.house_name = val.house_name;
          this.form.house_id = val.house_id;
          this.prefill(val.house_res, 'house');
        }
        if (t.city !== undefined && t.city !== '') {
          let val = JSON.parse(t.city);
          this.form.community = val;
          this.community_name = val.village_name;
        }
        if (t.staff !== undefined && t.staff !== '') {
          let val = JSON.parse(t.staff);
          this.form.staff_id = val.staff_id;
          this.form.staff_name = val.staff_name;
          this.form.department_id = val.depart_id;
          this.form.department_name = val.depart_name;
          this.isValue1 = val.activeRevise;
          this.stick();
        }
        if (t.depart !== undefined && t.depart !== '') {
          let val = JSON.parse(t.depart);
          this.form.department_name = val.name;
          this.form.department_id = val.id;
          this.isValue1 = val.activeRevise;
          this.stick();
        }
        if (t.tops === '') {
          this.stick();
        }
        this.userInfo(this.isValue1);
      },

      qualityDetail(val) {
        this.form.processable_id = '';
        let type;
        if (val !== '') {
          type = 'bulletin/quality/' + val.newID;
          if (val.type === 2) {
            this.form.processable_id = val.ids;
          } else {
            this.userInfo(true);
          }
        } else {
          this.userInfo(true);
          type = 'bulletin/quality';
        }
        this.$http.get(this.urls + type).then((res) => {
          if (res.data.code === "51420") {
            let data = res.data.data;
            this.form.id = res.data.id;
            this.form.house_id = data.house_id;
            if (data.quality_up && data.quality_up !== undefined) {
              this.form.quality_up = String(data.quality_up);
              this.numbers = String(data.quality_up);
              if (String(data.quality_up) === '1') {
                this.followUp = true;
                this.house_name = data.address;
              } else {
                this.followUp = false;
                this.house_name = '';
              }
            } else {
              this.followUp = false;
              data.quality_up = '0';
              this.form.quality_up = '0';
            }
            this.prefill(res.data.data, 'draught');

            if (val !== '' && val.type === 2) {
              this.form.staff_id = data.staff_id;
              this.form.staff_name = data.staff_name;
              this.form.department_id = data.department_id;
              this.form.department_name = data.department_name;
            }
          } else {
            this.form.id = '';
          }
        })
      },

      prefill(data, val) {
        this.isClear = false;
        this.form.city_id = data.city_id;                     //
        this.form.province_id = data.province_id;             //
        this.form.city_name = data.city_name;                 //
        this.form.community = data.community;                 //id
        this.community_name = data.community.village_name;    //id
        this.form.door_address = data.door_address;

        this.form.house_type = data.house_type;
        let house = data.house_type;
        let room = dicts.value2[house[1]] === '' ? '0' : dicts.value2[house[1]];
        let hall = dicts.value3[house[2]] === '' ? '0' : dicts.value3[house[2]];
        this.house_type_name = dicts.value1[house[0] - 1] + room + hall;

        this.form.decorate = data.decorate;

        this.form.area = data.area;                                     //
        this.form.direction = data.direction;                           //
        this.form.property_type = data.property_type;                   //
        this.form.floor = data.floor;                                   //
        this.form.floors = data.floors;                                 //
        this.form.price = data.price;                                   //
        this.form.is_agency = data.is_agency;                           //
        this.cusFrom = dicts.value8[data.is_agency];                    //
        this.form.air_condition = data.air_condition;                   //
        this.air_condition_name = dicts.value5[data.air_condition];     //
        this.form.fridge = data.fridge;                                 //
        this.fridge_name = dicts.value5[data.fridge];                   //
        this.form.television = data.television;                         //
        this.television_name = dicts.value5[data.television];           //
        this.form.gas_stove = data.gas_stove;                           //
        this.gas_stove_name = dicts.value4[data.gas_stove];             //
        this.form.hood = data.hood;                                     //
        this.hood_name = dicts.value4[data.hood];                       //
        this.form.microwave = data.microwave;                           //
        this.microwave_name = dicts.value5[data.microwave];             //
        this.form.wash_machine = data.wash_machine;                     //
        this.wash_machine_name = dicts.value5[data.wash_machine];       //
        this.form.water_heater = data.water_heater;                     //
        this.water_heater_name = dicts.value5[data.water_heater];       //
        this.form.sofa = data.sofa;                                     //
        this.sofa_name = dicts.value4[data.sofa];                       //
        this.form.clothe_rack = data.clothe_rack;                       //
        this.clothe_rack_name = dicts.value4[data.clothe_rack];         //
        this.form.heater = data.heater;                                 //
        this.heaterOn = data.heater === 1 ? true : false;               //
        this.form.gas = data.gas;                                       //
        this.gasOn = data.gas === 1 ? true : false;                     //
        this.form.bed = data.bed;                                       //
        this.bedOn = data.bed === 1 ? true : false;                     //
        this.form.bed_remark = data.bed_remark;                         //
        this.form.wardrobe = data.wardrobe;                             //
        this.wardrobeOn = data.wardrobe === 1 ? true : false;           //
        this.form.wardrobe_remark = data.wardrobe_remark;               //
        this.form.curtain = data.curtain;                               //
        this.curtainOn = data.curtain === 1 ? true : false;             //
        this.form.curtain_remark = data.curtain_remark;                 //
        this.form.is_fill = data.is_fill;                               //
        this.is_fillOn = data.is_fill === 1 ? true : false;             //
        this.form.is_lord_fill = data.is_lord_fill;                     //
        this.is_lordOn = data.is_lord_fill === 1 ? true : false;        //
        this.form.is_lord_fill_days = data.is_lord_fill_days;           //
        this.form.dining_table = data.dining_table;                     //
        this.form.chair = data.chair;                                   //
        this.form.is_clean = data.is_clean;                             //
        this.is_cleanOn = data.is_clean === 1 ? true : false;           //
        this.form.other_remark = data.other_remark;                     //
        this.form.other_furniture = data.other_furniture;               //
        if (val === 'draught') {
          this.photos = data.photo;                                       //
          this.form.photo = [];
          //
          for (let i = 0; i < data.photo.length; i++) {
            this.form.photo.push(data.photo[i].id);                       //
          }
        }
      },

      close_() {
        this.isClear = true;
        setTimeout(() => {
          this.isClear = false;
        });
        this.userInfo(true);
        $('.imgItem').remove();
        this.picStatus = true;
        this.form.id = '';
        this.form.processable_id = '';
        this.form.house_id = '';
        this.house_name = '';
        this.form.city_id = this.beforeCityId;            //
        this.form.province_id = this.beforeProvinceId;    //
        this.form.city_name = this.beforeCity;            //
        this.form.community = {};                         //id
        this.community_name = '';                         //
        this.form.door_address = ['', '', ''];

        this.form.house_type = [1, 1, 1];
        this.house_type_name = '111';

        this.form.area = '';                      //
        this.form.direction.name = '';            //
        this.form.direction.id = '';              //
        this.form.property_type.id = '';          //
        this.form.property_type.name = '';        //
        this.form.decorate.id = '';             //
        this.form.decorate.name = '';           //
        this.form.floor = '';                    //
        this.form.floors = '';                   //
        this.form.price = '';                    //
        this.form.is_agency = '';                 //
        this.cusFrom = '';                  //
        this.form.air_condition = 1;           //
        this.form.fridge = 1;                       //
        this.form.television = 1;              //
        this.form.gas_stove = 1;               //
        this.form.hood = 1;                       //
        this.form.microwave = 1;               //
        this.form.wash_machine = 1;            //
        this.form.water_heater = 1;            //
        this.form.sofa = 1;                   //
        this.form.clothe_rack = 1;             //
        this.form.heater = 1;                    //
        this.heaterOn = true;               //
        this.form.gas = 1;                           //
        this.gasOn = true;                  //
        this.form.bed = 1;                  //
        this.bedOn = true;                    //
        this.form.bed_remark = '';               //
        this.form.wardrobe = 1;                  //
        this.wardrobeOn = true;                 //
        this.form.wardrobe_remark = '';          //
        this.form.curtain = 1;                   //
        this.curtainOn = true;                    //
        this.form.curtain_remark = '';           //
        this.form.is_fill = 1;                   //
        this.is_fillOn = true;                    //
        this.form.is_lord_fill = 0;               //
        this.is_lordOn = true;                   //
        this.form.is_lord_fill_days = '';        //
        this.form.dining_table = '';             //
        this.form.chair = '';                     //
        this.form.is_clean = 1;                 //
        this.is_cleanOn = true;                  //
        this.form.other_remark = '';             //
        this.form.other_furniture = '';          //
        this.form.photo = [];                    //
        this.photos = [];                    //
      },
    },
  }
</script>

<style lang="scss">
  #quality {
    overflow: hidden;
    @mixin flex {
      display: flex;
      display: -webkit-flex;
    }
    .unit {
      @include flex;
      b {
        padding: 0 .3rem;
        height: 44px;
        line-height: 44px;
        border-bottom: 1px solid #F4F4F4;
      }
    }

    .titleSwitch {
      background: #FFFFFF;
      border-bottom: 1px solid #F4F4F4;
      .cellGroup {
        @include flex;
        align-items: center;
        justify-content: space-between;
        .van-cell {
          width: 66px;
        }
        .cellA {
          @include flex;
          align-items: center;
          margin-left: -7px;
          .requiredIcon {
            color: #f44;
          }
        }
      }
    }
  }
</style>



